

.loader-box{
  width: 100px;
  height: 100px;
  border-radius: 50%;
  border: 3px dashed red;
  animation: rotate 1s infinite linear;
  position: relative;

}

@keyframes rotate {
  from{
    transform: rotate(0deg) scale(1);
  }
  to{
    transform: rotate(360deg)scale(1.4);

  }

}