
.upload-autoParts-image{
  width: 100px;
  height: 100px;
  position: relative;
  border-radius: 10px;
  margin: 10px 0 0 0;

  span{
     position: absolute;
    left: 0;
    top: 0;
    color: red;
    cursor: pointer;
    transition: .3s;

    &:hover{
      color: aqua;
    }
  }
}